@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:"";content:none}table{border-collapse:collapse;border-spacing:0}*{font-family:"Roboto", sans-serif}body{background:#737373}.container{max-width:1200px;margin:0 auto}@media screen and (max-width: 414px){.container{margin:0 20px}}

.hero{background-image:linear-gradient(to top, rgba(0,0,0,0.5), rgba(0,0,0,0.5)),url(/ITN_Bootcamp/static/media/hero-bg.b236af76.jpg);background-size:cover;padding:80px 0 178px}.hero-partners{display:flex;align-items:center}.hero-partners-item{margin-left:60px}.hero-partners-item:first-child{margin-left:0}.hero-navigation{margin-top:45px}.hero-navigation-item{text-decoration:none;font-weight:normal;font-size:19px;line-height:22px;color:#fff;margin-left:75px}.hero-navigation-item:first-child{margin-left:0}.hero-title{width:664px;font-weight:normal;font-size:57px;line-height:67px;color:#fff;margin:108px 0 132px}.hero-link{display:inline-block;text-decoration:none;padding:35px 151px;font-weight:normal;font-size:32px;line-height:37px;color:#fff;background:#b78a3f}.hero-burgerBtn{display:none}@media screen and (max-width: 414px){.hero{background-image:linear-gradient(to top, rgba(0,0,0,0.5), rgba(0,0,0,0.5)),url(/ITN_Bootcamp/static/media/hero-bg-iPhone.ca45f18c.jpg);padding:8px 0 75px;position:relative}.hero-partners{width:266px;height:86px;margin-left:7px}.hero-partners-item{height:57%;margin-left:22px}.hero-burgerBtn{display:block;position:absolute;right:20px;top:40px;z-index:10}.hero-burgerBtn-desable{pointer-events:none}.hero-navigation{display:none}.hero-title{width:374px;font-weight:normal;font-size:32px;line-height:37px;margin:215px 0 267px}.hero-link{padding:24px 140px;font-size:28px;line-height:33px;color:#fff;background:#b78a3f;margin:-20px}}

.menu{display:none}.menu-closeBtn{display:none}@media screen and (max-width: 414px){.menu{display:block;background:#737373;position:absolute;left:0;top:0;text-align:center;padding:40px 0 10px;transform:translateX(-100%);transition:all 0.3s}.menu.active{transform:translateX(0%)}.menu-closeBtn{display:block;position:absolute;left:20px;top:40px}.menu-nav{margin:247px 0 240px}.menu-nav-item{display:block;text-decoration:none;font-weight:normal;font-size:23px;line-height:27px;color:#fff;margin-bottom:40px}.menu-nav-item:last-child{margin-bottom:0}.menu-text{font-weight:300;font-size:15px;line-height:15px;color:#fff;margin-bottom:30px}}

.facts{box-sizing:border-box;padding:100px 60px 50px;position:relative}.facts-title{font-weight:normal;font-size:35px;line-height:41px;text-align:center;color:#fff;margin-bottom:30px}.facts-slide-desc{margin:40px 0 66px;font-weight:normal;font-size:27px;line-height:32px;text-align:center;color:#fff}.facts>.swiper-button-prev,.facts .swiper-button-next{color:#b78a3f;position:absolute}.facts>.swiper-button-prev{left:10px}.facts>.swiper-button-next{right:10px}.facts>.swiper-button-disabled{color:transparent}.facts-pagination{display:flex;justify-content:center}.facts-pagination-bullet{display:inline-block;height:24px;width:24px;border-radius:50%;border:2px solid #b78a3f;background-color:transparent;opacity:1;margin:0 20px;transition:0.3s background-color}.facts-pagination>.swiper-pagination-bullet-active{background-color:#b78a3f}@media screen and (max-width: 414px){.facts{padding:70px 30px 70px;margin:0}.facts-title{font-size:27px;line-height:32px;margin-bottom:30px}.facts-slide-desc{font-size:17px;line-height:20px;margin:20px 0}.facts>.swiper-button-next::after,.facts>.swiper-button-prev::after{font-size:28px}.facts>.swiper-button-next{right:0}.facts>.swiper-button-prev{left:0}.facts-pagination-bullet{margin:0 10px;height:12px;width:12px}.swiper-slide-active{z-index:1}.swiper-slide-active .facts-slide{margin:0 auto;width:274px;overflow:hidden}.swiper-slide-next{z-index:0}.swiper-slide-next .facts-slide{transform:translate(-70%, 0) scale(0.9)}.swiper-slide-next .facts-slide-desc{display:none}.swiper-slide-prev{z-index:0}.swiper-slide-prev .facts-slide{transform:translate(70%, 0) scale(0.9)}.swiper-slide-prev .facts-slide-desc{display:none}}

.sectors{padding:50px 0}.sectors-title{font-weight:normal;font-size:35px;line-height:41px;color:#fff;text-align:center}.sectors-subtitle{font-weight:normal;font-size:23px;line-height:27px;color:#fff;margin:42px 0 30px}.sectors-list{display:flex;flex-wrap:wrap;justify-content:space-between}.sectors-list-item{position:relative;width:calc((100% / 3) - 70px);height:240px;margin-bottom:30px;display:flex;justify-content:center;align-items:center}.sectors-list-item-name{background-color:#fff;width:100%;text-align:center;font-size:23px;line-height:30px;color:#222222}.sectors-list-item-tooltip{position:absolute;width:440px;left:100%;pointer-events:none;-webkit-appearance:none;-moz-appearance:none;appearance:none;background-color:#dadada;padding:16px 16px 16px 75px;-webkit-clip-path:polygon(12% 2%, 100% 0, 100% 100%, 12% 100%, 2% 50%);clip-path:polygon(12% 2%, 100% 0, 100% 100%, 12% 100%, 2% 50%);z-index:1;-webkit-animation:fadeIn 0.4s ease-in-out;animation:fadeIn 0.4s ease-in-out}.sectors-list-item-tooltip-switched{left:auto;left:initial;right:100%;padding:16px 75px 16px 16px;-webkit-clip-path:polygon(0 0, 85% 0, 100% 47%, 85% 100%, 0 100%);clip-path:polygon(0 0, 85% 0, 100% 47%, 85% 100%, 0 100%)}@media screen and (max-width: 414px){.sectors{padding:20px 0 50px}.sectors-title{font-size:27px;line-height:32px}.sectors-subtitle{display:none}.sectors-list{display:block}.sectors-list-item{position:relative;width:390px;height:240px;margin:30px 0 0}.sectors-list-item-name{font-size:23px;line-height:27px}.sectors-list-item-tooltip{display:none}}

.footer{border-top:1px solid #fff}.footer-partners{display:flex;align-items:center}.footer-partners-item{margin-right:120px}.footer-partners-item:last-child{margin-right:0}.footer-text-wrap{display:flex;justify-content:space-between}.footer-nav{display:flex;flex-direction:column}.footer-nav-link{font-weight:normal;font-size:17px;line-height:20px;text-decoration:none;color:#fff;margin-bottom:20px}.footer-nav-link:last-child{margin-bottom:88px}.footer-nav-text{width:366px;font-weight:normal;font-size:17px;line-height:20px;color:#fff}.footer-text{font-weight:300;font-size:14px;line-height:14px;color:#fff;margin-bottom:14px}.footer .text-yellow{color:#ede852}@media screen and (max-width: 414px){.footer-partners{display:flex;justify-content:space-between;align-items:center}.footer-partners-item{height:47px;margin-right:80px}.footer-partners-item:last-child{margin-right:0}.footer-text-wrap{display:block;text-align:center;margin:58px 0 88px}.footer-nav-link{font-size:15px;line-height:18px;margin-bottom:13px}.footer-nav-link:last-child{margin-bottom:112px}.footer-nav-text{font-size:15px;line-height:18px}.footer-text{font-size:10px;line-height:10px;text-align:center;margin-bottom:10px}}

.air-q{padding:50px 0}.air-q-title{text-align:center;font-weight:normal;font-size:35px;line-height:41px;color:#fff}.air-q-map{height:560px;margin:42px 0}.air-q-text{font-weight:normal;font-size:23px;line-height:27px;color:#fff}@media screen and (max-width: 414px){.air-q{padding:20px 0 50px}.air-q-title{font-size:27px;line-height:32px}.air-q-text{font-size:17px;line-height:20px}}

