.air-q {
  padding: 50px 0;
  &-title {
    text-align: center;
    font-weight: normal;
    font-size: 35px;
    line-height: 41px;
    color: #fff;
  }
  &-map {
    height: 560px;
    margin: 42px 0;
  }
  &-text {
    font-weight: normal;
    font-size: 23px;
    line-height: 27px;
    color: #fff;
  }
}
@media screen and (max-width: 414px) {
  .air-q {
    padding: 20px 0 50px;
    &-title {
      font-size: 27px;
      line-height: 32px;
    }
    &-text {
      font-size: 17px;
      line-height: 20px;
    }
  }
}
