.menu {
  display: none;
  &-closeBtn {
    display: none;
  }
}

@media screen and (max-width: 414px) {
  .menu {
    display: block;
    background: #737373;
    position: absolute;
    left: 0;
    top: 0;
    text-align: center;
    padding: 40px 0 10px;
    transform: translateX(-100%);
    transition: all 0.3s;
    &.active {
      transform: translateX(0%);
    }

    &-closeBtn {
      display: block;
      position: absolute;
      left: 20px;
      top: 40px;
    }

    &-nav {
      margin: 247px 0 240px;
      &-item {
        display: block;
        text-decoration: none;
        font-weight: normal;
        font-size: 23px;
        line-height: 27px;
        color: #fff;
        margin-bottom: 40px;
        &:last-child {
          margin-bottom: 0;
        }
      }
    }
    &-text {
      font-weight: 300;
      font-size: 15px;
      line-height: 15px;
      color: #fff;
      margin-bottom: 30px;
    }
  }
}
