.facts {
  box-sizing: border-box;
  padding: 100px 60px 50px;
  position: relative;

  &-title {
    font-weight: normal;
    font-size: 35px;
    line-height: 41px;
    text-align: center;
    color: #fff;
    margin-bottom: 30px;
  }
  &-slide {
    &-desc {
      margin: 40px 0 66px;
      font-weight: normal;
      font-size: 27px;
      line-height: 32px;
      text-align: center;
      color: #fff;
    }
  }
  & > .swiper-button-prev,
  .swiper-button-next {
    color: #b78a3f;
    position: absolute;
  }
  & > .swiper-button-prev {
    left: 10px;
  }
  & > .swiper-button-next {
    right: 10px;
  }
  & > .swiper-button-disabled {
    color: transparent;
  }
  &-pagination {
    display: flex;
    justify-content: center;

    &-bullet {
      display: inline-block;
      height: 24px;
      width: 24px;
      border-radius: 50%;
      border: 2px solid #b78a3f;
      background-color: transparent;
      opacity: 1;
      margin: 0 20px;
      transition: 0.3s background-color;
    }
    & > .swiper-pagination-bullet-active {
      background-color: #b78a3f;
    }
  }
}
@media screen and (max-width: 414px) {
  .facts {
    padding: 70px 30px 70px;
    margin: 0;
    &-title {
      font-size: 27px;
      line-height: 32px;
      margin-bottom: 30px;
    }
    &-slide {
      &-desc {
        font-size: 17px;
        line-height: 20px;
        margin: 20px 0;
      }
    }
    & > .swiper-button {
      &-next,
      &-prev {
        &::after {
          font-size: 28px;
        }
      }
      &-next {
        right: 0;
      }
      &-prev {
        left: 0;
      }
    }
    &-pagination {
      &-bullet {
        margin: 0 10px;
        height: 12px;
        width: 12px;
      }
    }
  }
  .swiper-slide-active {
    z-index: 1;
    .facts-slide {
      margin: 0 auto;
      width: 274px;
      overflow: hidden;
    }
  }
  .swiper-slide-next {
    z-index: 0;
    .facts-slide {
      transform: translate(-70%, 0) scale(0.9);
      &-desc {
        display: none;
      }
    }
  }
  .swiper-slide-prev {
    z-index: 0;
    .facts-slide {
      transform: translate(70%, 0) scale(0.9);
      &-desc {
        display: none;
      }
    }
  }
}
