.hero {
  background-image: linear-gradient(
      to top,
      rgba(0, 0, 0, 0.5),
      rgba(0, 0, 0, 0.5)
    ),
    url("../../assets/hero-bg.jpg");
  background-size: cover;
  padding: 80px 0 178px;

  &-partners {
    display: flex;
    align-items: center;

    &-item {
      margin-left: 60px;

      &:first-child {
        margin-left: 0;
      }
    }
  }

  &-navigation {
    margin-top: 45px;
    &-item {
      text-decoration: none;
      font-weight: normal;
      font-size: 19px;
      line-height: 22px;
      color: #fff;
      margin-left: 75px;
      &:first-child {
        margin-left: 0;
      }
    }
  }

  &-title {
    width: 664px;
    font-weight: normal;
    font-size: 57px;
    line-height: 67px;
    color: #fff;
    margin: 108px 0 132px;
  }
  &-link {
    display: inline-block;
    text-decoration: none;
    padding: 35px 151px;
    font-weight: normal;
    font-size: 32px;
    line-height: 37px;
    color: #fff;
    background: #b78a3f;
  }
  &-burgerBtn {
    display: none;
  }
}

//iPhone 11 Pro Max-1
@media screen and (max-width: 414px) {
  .hero {
    background-image: linear-gradient(
        to top,
        rgba(0, 0, 0, 0.5),
        rgba(0, 0, 0, 0.5)
      ),
      url("../../assets/hero-bg-iPhone.jpg");
    padding: 8px 0 75px;
    position: relative;
    //overflow-x: hidden;

    &-partners {
      width: 266px;
      height: 86px;
      margin-left: 7px;
      &-item {
        height: 57%;
        margin-left: 22px;
      }
    }

    &-burgerBtn {
      display: block;
      position: absolute;
      right: 20px;
      top: 40px;
      z-index: 10;
      &-desable {
        pointer-events: none;
      }
    }

    &-navigation {
      display: none;
    }
    &-title {
      width: 374px;
      font-weight: normal;
      font-size: 32px;
      line-height: 37px;
      margin: 215px 0 267px;
    }
    &-link {
      padding: 24px 140px;
      font-size: 28px;
      line-height: 33px;
      color: #fff;
      background: #b78a3f;
      margin: -20px;
    }
  }
}
