.footer {
  border-top: 1px solid #fff;
  &-partners {
    display: flex;
    align-items: center;
    &-item {
      margin-right: 120px;
      &:last-child {
        margin-right: 0;
      }
    }
  }
  &-text {
    &-wrap {
      display: flex;
      justify-content: space-between;
    }
  }
  &-nav {
    display: flex;
    flex-direction: column;
    &-link {
      font-weight: normal;
      font-size: 17px;
      line-height: 20px;
      text-decoration: none;
      color: #fff;
      margin-bottom: 20px;

      &:last-child {
        margin-bottom: 88px;
      }
    }
    &-text {
      width: 366px;
      font-weight: normal;
      font-size: 17px;
      line-height: 20px;
      color: #fff;
    }
  }
  &-text {
    font-weight: 300;
    font-size: 14px;
    line-height: 14px;
    color: #fff;
    margin-bottom: 14px;
  }
  .text-yellow {
    color: #ede852;
  }
}
@media screen and (max-width: 414px) {
  .footer {
    &-partners {
      display: flex;
      justify-content: space-between;
      align-items: center;
      &-item {
        height: 47px;
        margin-right: 80px;
        &:last-child {
          margin-right: 0;
        }
      }
    }
    &-text {
      &-wrap {
        display: block;
        text-align: center;
        margin: 58px 0 88px;
      }
    }
    &-nav {
      &-link {
        font-size: 15px;
        line-height: 18px;
        margin-bottom: 13px;

        &:last-child {
          margin-bottom: 112px;
        }
      }
      &-text {
        font-size: 15px;
        line-height: 18px;
      }
    }
    &-text {
      font-size: 10px;
      line-height: 10px;
      text-align: center;
      margin-bottom: 10px;
    }
  }
}
