.sectors {
  padding: 50px 0;
  &-title {
    font-weight: normal;
    font-size: 35px;
    line-height: 41px;
    color: #fff;
    text-align: center;
  }
  &-subtitle {
    font-weight: normal;
    font-size: 23px;
    line-height: 27px;
    color: #fff;
    margin: 42px 0 30px;
  }
  &-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    &-item {
      position: relative;
      width: calc((100% / 3) - 70px);
      height: 240px;
      margin-bottom: 30px;
      display: flex;
      justify-content: center;
      align-items: center;

      &-name {
        background-color: #fff;
        width: 100%;
        text-align: center;
        font-size: 23px;
        line-height: 30px;
        color: #222222;
      }
      &-tooltip {
        position: absolute;
        width: 440px;
        left: 100%;
        pointer-events: none;
        appearance: none;
        background-color: #dadada;
        padding: 16px 16px 16px 75px;
        clip-path: polygon(12% 2%, 100% 0, 100% 100%, 12% 100%, 2% 50%);
        //opacity: 0;
        z-index: 1;
        animation: fadeIn 0.4s ease-in-out;

        &-switched {
          left: initial;
          right: 100%;
          padding: 16px 75px 16px 16px;
          clip-path: polygon(0 0, 85% 0, 100% 47%, 85% 100%, 0 100%);
        }
      }
    }
  }
}
@media screen and (max-width: 414px) {
  .sectors {
    padding: 20px 0 50px;
    &-title {
      font-size: 27px;
      line-height: 32px;
    }
    &-subtitle {
      display: none;
    }
    &-list {
      display: block;
      &-item {
        position: relative;
        width: 390px;
        height: 240px;
        margin: 30px 0 0;

        &-name {
          font-size: 23px;
          line-height: 27px;
        }
        &-tooltip {
          display: none;
        }
      }
    }
  }
}
